<template>
<div class="container">
  <div v-if="!detailView">
    <h2>
      <i class="el-icon-date"></i>
      
    </h2>

    <div class="next">
      <div>
         2023-03-03 
        <i class="el-icon-edit" style="cursor: pointer;" @click="dialogVisible = true"></i>
      </div>
      <div>
        <span>: </span>
        <span>{{ records[nextCheck].time }}</span>
      </div>
      <div>{{nextCheck + 1}}{{ 12 + 4 * nextCheck }}</div>
    </div>

    <div class="box">
      <div v-for="(item, i) in records" :key="i" class="item">
        <div style="flex: 1; cursor: pointer;" @click="toDetail(item)">
          <div style="font-size: 14px; font-weight: bold; color: #333; margin-bottom: 5px;">{{ i + 1 }}{{ 12 + 4 * i }}</div>
          <div> {{ item.content }}</div>
          <div>{{ item.time }}</div>
        </div>
        <div class="tick" :class="{'is-tick': item.tick}" @click="tick(i)">
          <i class="el-icon-circle-check"></i>
        </div>
      </div>
    </div>

    <div style="padding: 10px; background: #eee;">
      <el-calendar>
        <template
          v-slot:dateCell="{data}">
          <div :class="{'check-day': checkDays.includes(data.day)}">
            {{ data.day.split('-')[2] }}
            <div v-if="checkDays.includes(data.day)">
              
            </div>
          </div>
        </template>
      </el-calendar>
    </div>
  </div>

  <div class="detail" v-if="detailView">
    <div class="detail-header">
      <el-button plain @click="detailView = false;"></el-button>
      <h3>{{ detail.t }}</h3>
      <el-button type="danger" @click="detail.tick = !detail.tick">{{detail.tick ? '' : ''}}</el-button>
    </div>
    <p> {{8 + 4 * detail.t}}</p>
    <p> {{ detail.time }}</p>
    <p> </p>
    <p style="text-indent: 2em;">3512</p>
    <p> </p>
    <p style="text-indent: 2em;">{{ detail.content }}</p>
    <p> </p>
    <p style="text-indent: 2em;">: </p>
    <p style="text-indent: 2em;">: 10-12.5</p>
    <p style="text-indent: 2em;">: </p>
    <p style="text-indent: 2em;">: 10-12.5</p>
  </div>

  <el-dialog
    title=""
    :visible.sync="dialogVisible"
    width="30%">
    <el-date-picker
      v-model="pregnantTime"
      type="date"
      placeholder="">
    </el-date-picker>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="dialogVisible = false"> </el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: "inspection-record",
  data() {
    return {
      records: [
        { t: 1, tick: false, project: 'BMDI+(ABO+RH)+HIV ()', content: 'HIV', time: '2023-05-26' },
        { t: 2, tick: false, project: 'BMDI+(ABO+RH)+HIV ()', content: '', time: '2023-06-23' },
        { t: 3, tick: false, project: 'BMDI+(ABO+RH)+HIV ()', content: '', time: '2023-07-21' },
        { t: 4, tick: false, project: 'BMDI+(ABO+RH)+HIV ()', content: '', time: '2023-08-18' },
        { t: 5, tick: false, project: 'BMDI+(ABO+RH)+HIV ()', content: '', time: '2023-09-15' },
        { t: 6, tick: false, project: 'BMDI+(ABO+RH)+HIV ()', content: '', time: '2023-09-29' },
      ],
      checkDays: [],
      detail: {},
      detailView: false,
      dialogVisible: false,
      pregnantTime: new Date('2023-03-03')
    }
  },
  created() {
    this.checkDays = this.records.map(i => i.time);
  },
  computed: {
    nextCheck() {
      return this.records.findIndex(i => !i.tick);
    }
  },
  methods: {
    tick(i) {
      if (this.records[i - 1] && this.records[i - 1].tick === false) {
        this.$message.warning('~');
        return;
      }
      this.records[i].tick = !this.records[i].tick;
    },
    toDetail(data) {
      this.detail = data;
      this.detailView = true;
    }
  }
}
</script>

