<template>
  <div class="container">
    <el-button type="text" v-if="!isLogin" @click="toLogin"></el-button>
    <el-button type="text" v-if="!isLogin" @click="toRegister"></el-button>
    <el-dropdown v-if="isLogin" @command="toOrder">
      <el-button type="text" style="height: 100%;"> {{ username }}</el-button>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="inspection-record"></el-dropdown-item>
        <el-dropdown-item command="data-record"></el-dropdown-item>
        <el-dropdown-item command="order"></el-dropdown-item>
        <el-dropdown-item command="logout"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>


    <el-dialog :visible.sync="visible" title="" :center="true" :close-on-click-modal="false" :destroy-on-close="true">
      <el-form :model="form" :rules="rules">
        <el-form-item label="" prop="username">
          <el-input v-model="form.username" placeholder="//"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="form.password" show-password placeholder=""></el-input>
        </el-form-item>
        <el-button class="register-btn" type="text" @click="register"></el-button>
        <el-button class="login-btn" type="primary" @click="login"></el-button>
      </el-form>
    </el-dialog>

    <el-dialog :visible.sync="registerVisible" title="" :center="true" :close-on-click-modal="false" :destroy-on-close="true">
      <el-form :model="registerForm" :rules="registerRules">
        <el-form-item label="" prop="username">
          <el-input v-model="registerForm.username" placeholder="" maxlength="10"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="registerForm.phone" placeholder="" maxlength="10"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="registerForm.email" placeholder="" maxlength="10"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="registerForm.password" show-password placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input v-model="registerForm.confirmPassword" show-password placeholder=""></el-input>
        </el-form-item>
        <el-button class="login-btn" type="primary" @click="register"></el-button>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "User",
  data() {
    return {
      isLogin: false,
      visible: false,
      registerVisible: false,
      username: '',
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }]
      },
      registerForm: {
        username: '',
        phone: '',
        email: '',
        password: '',
        confirmPassword: ''
      },
      registerRules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        confirmPassword: [{ required: true, message: '', trigger: 'blur' }]
      }
    };
  },
  methods: {
    toLogin() {
      this.visible = true;
    },
    toRegister() {
      this.registerVisible = true;
    },
    login() {
      this.username = this.form.username;
      this.visible = false;
      this.isLogin = true;
    },
    register() {
      this.registerVisible = false;
      this.visible = true;
    },
    toOrder(command) {
      if (command === 'logout') {
        this.isLogin = false;
        this.$router.push('/home');
      }
      command === 'order' && this.$router.push('/order');
      command === 'inspection-record' && this.$router.push('/inspection-record');
      command === 'data-record' && this.$router.push('/data-record');
    }
  }
}
</script>

<style scoped>
.container {
  height: 60px;
  display: flex;
  align-items: stretch;
}

.container > .el-button {
  padding: 0 15px;
  color: #fff;
}
.container > .el-button:hover {
  background: #e539ba;
}

.register-btn {
  float: right;
}

.login-btn {
  margin-top: 10px;
  width: 100%;
}

</style>