<template>
    <el-card class="container">
        <div slot="header" style="display: flex; align-items: center; justify-content: space-between">
            <span class="title"></span>
            <div v-if="!isDetail">
                <el-button @click="help = true"><i class="el-icon-info"></i> </el-button>
                <el-button @click="add = true" type="primary"><i class="el-icon-edit"></i> </el-button>
            </div>
        </div>

        <div v-if="!isDetail">
            <div class="item" v-for="(item, i) in records" :key="i" @click="toDetail(item)">
                <i class="el-icon-caret-right"></i>
                {{ item.title }}
                <span style="float: right; color: #999;">{{ item.time }}</span>
            </div>
        </div>
        <div class="detail" v-else>
            <div class="detail-header">
                <el-button style="position: absolute; left: 10px;" plain @click="isDetail = false;"></el-button>
                <h3>{{ current.title }}</h3>
            </div>
            <p v-html="current.content"></p>
        </div>

        <el-dialog
            title=""
            :visible.sync="help"
            width="60%">
            <div style="text-align: justify; word-break: break-word;">
                2~3
                <br/><br/>1.120~160
                <br/><br/>2.BPD9.3cm5287BPD7.0cm328.0cm80.2cm
                <br/><br/>3.1.52cm2.55cm
                <br/><br/>4.2.5~5cm30~32
                <br/><br/>5.3~7cm7cm3cm
                <br/><br/>6.
                <br/><br/>7.
                <br/><br/>8.BPD2~3cmBPD9.3cm7.3cmBPD8.9cm6.9cm
                <br/><br/>9.10.14~387.60.6mm10mm410~15mm1/315mm43%
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="help = false"> </el-button>
            </span>
        </el-dialog>

        <el-dialog
            title=""
            :visible.sync="add"
            width="60%">
            <div style="height: 400px;">
                <div style="display: flex; align-items: center;"><span style="width: 100px;"></span> <el-input v-model="title"></el-input></div>
                <quillEditor v-model="content" style="box-sizing: border-box; padding-top: 20px; width: 100%; height: calc(100% - 100px)"></quillEditor>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="add = false"> </el-button>
                <el-button type="primary" @click="confirm"> </el-button>
            </span>
        </el-dialog>
    </el-card>
</template>

<script>
import { quillEditor } from "vue-quill-editor"

export default {
    name: 'data-record',
    components: {
        quillEditor
    },
    data() {
        return {
            records: [
                { title: '', content: '', time: '2023-03-03' },
                { title: '', content: '', time: '2023-03-06' },
                { title: '', content: '', time: '2023-03-13' },
                { title: '', content: '', time: '2023-03-16' },
                { title: '', content: '', time: '2023-03-21' },
                { title: '', content: '', time: '2023-03-28' },
            ],
            help: false,
            add: false,
            title: '',
            content: '',
            isDetail: false,
            current: {},
            txt: `
            1.120~160
            <br/>2.BPD9.3cm5287BPD7.0cm328.0cm80.2cm
            <br/>3.1.52cm2.55cm
            <br/>4.2.5~5cm30~32`
        }
    },
    created() {
        this.records.reverse();
        this.records.map(i => {
            i.content = this.txt;
            return i;
        })
    },
    methods: {
        toDetail(data) {
            this.current = data;
            this.isDetail = true;
        },
        confirm() {
            this.add = false;
            this.records.unshift({ title: this.title , content: this.content, time: this.getTime() });
            this.title = '';
            this.content = '';
        },
        getTime(date) {
            date = date ? new Date(date) : new Date();
            return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${date.getDate()}`;
        }
    }
}
</script>

